
.copy-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: var(--primary-color);
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
}

.copy-button:hover {
  background-color: var(--secondary-color);
}
